 "use client";
import Link from "next/link";
import { useState, useEffect } from "react";
import Profile from "../components/Profile";
import { auth } from "../lib/firebase";
import { onAuthStateChanged, signOut } from "firebase/auth";
import AuthModal from "../components/AuthModal";
import PropTypes from "prop-types";

const Navbar = ({ setShowProfile, showProfile }) => {
  const [user, setUser] = useState(null);
  const [isAuthModalOpen, setIsAuthModalOpen] = useState(false);

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (currentUser) => {
      setUser(currentUser);
    });
    return () => unsubscribe();
  }, []);

  const handleLogout = async () => {
    await signOut(auth);
    setShowProfile(false); // Close profile on logout
  };

  return (
    <>
      <nav className="flex justify-between items-center bg-gray-100 p-4">
        {/* Brand Logo */}
        <Link href="/" className="text-xl font-bold">
          Home
        </Link>

        <div className="relative flex items-center space-x-4">
          {/* Toggle Profile */}
          <button onClick={() => setShowProfile((prev) => !prev)} className="flex items-center">
            <img
              src={user?.photoURL || "https://tecdn.b-cdn.net/img/new/avatars/2.jpg"}
              className="rounded-full"
              style={{ height: "30px", width: "30px" }}
              alt="Profile"
              loading="lazy"
            />
          </button>

          {/* Show Profile Component and Pass setShowProfile */}
          {showProfile && <Profile setShowProfile={setShowProfile} />}
        </div>
      </nav>

      {/* Include AuthModal */}
      <AuthModal isOpen={isAuthModalOpen} onClose={() => setIsAuthModalOpen(false)} />
    </>
  );
};

Navbar.propTypes = {
  setShowProfile: PropTypes.func.isRequired,
  showProfile: PropTypes.bool.isRequired,
};

export default Navbar;
